import { Client } from "@rigdev/sdk/lib/index.js";
import { getInput } from "@actions/core";
import { load } from "js-yaml";
export async function makeClient() {
    const input = {
        url: getInput("url"),
        project: getInput("project"),
        clientID: getInput("clientID"),
        clientSecret: getInput("clientSecret"),
    };
    const client = new Client({
        host: input.url,
        projectID: input.project,
        credentials: {
            id: input.clientID,
            secret: input.clientSecret,
        },
    });
    return client;
}
export function parseDeployOutput(config) {
    let output = {};
    for (let k in config) {
        let obj = load(config[k]);
        output[k] = obj;
    }
    return output;
}
export function getBool(name) {
    return getInput(name) === "true";
}
